/* width */
::-webkit-scrollbar {
    width: 8px;
}
  
/* Track
::-webkit-scrollbar-track {
box-shadow: inset 0 0 5px grey; 
border-radius: 4px;
} */
  
::-webkit-scrollbar-corner {
    background: white;
}
   
/* Handle */
::-webkit-scrollbar-thumb {
    background:rgb(200, 200, 200); 
    border-radius: 4px;
}
  
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgb(100, 100, 100); 
}

